import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpErrorResponse, HttpHeaders } from '@angular/common/http';

import { of, Subject } from 'rxjs';
import { map, catchError, takeUntil, shareReplay, startWith } from 'rxjs/operators';

import { LoanInterface, WrongLoanList } from './loan.interface';

import { EntityStateService } from '../shared/services/entity-state.service';
import { IsEntityChangedInterface, EntityMessageTypeEnum, 
  EntityStateEnum, EntityChangeResultEnum } from '../shared/model/entity-state.interface';

import {ServerStatusService } from '../shared/services/server-status.service';

import { environment } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class LoanListService {
  private apiUrl = environment.apiUrl;

  private curLoanListSub$ = new Subject<any[] | LoanInterface[]>();
  public curLoanList$ = this.curLoanListSub$.asObservable()
                        .pipe(
                          startWith(WrongLoanList),
                          shareReplay(1)
                        );
  public curLoanList: LoanInterface[] = WrongLoanList;
  
  private onDestroySub$ = new Subject<boolean>();

  constructor(
    private http: HttpClient,
    private serverStatusSrv: ServerStatusService,
    ) {
      this.curLoanList$.subscribe(
        loans => {
          this.curLoanList = loans;
        }
      );
  }
  // ------------------------------------------------------------
  public isWrongLoanList(loans: LoanInterface[]): boolean {
    if (!loans) {
      throw new Error('Parameter [loans] is null');
    }
    if (loans === WrongLoanList) {
      return true;
    } else {
        return false;
    }
  }  
  public isLoanListNotEmpty(loans: LoanInterface[]): boolean {
    if (!loans) {
      throw new Error('Parameter [loans] is null');
    }
    if (loans === WrongLoanList) {
      return false;
    } else {
        return true;
    }
  }
  private isLoanListEmpty(): boolean {
    return !this.isLoanListNotEmpty(this.curLoanList);
  }
  public clearLoanList(): void {
    if (!this.isLoanListEmpty()) {
      this.curLoanListSub$.next(WrongLoanList);
    }
  }
// ------------------------------------------------------------
public loadLoanList(stateService: EntityStateService): void {
  // console.log('SYNC ENTER LoanListService.loadLoanList() %O', stateService);

  if (stateService) {
    const isClentsChangedEnter: IsEntityChangedInterface = {
      op: EntityStateEnum.LOAD,
      isEnd: false,
      opResult: EntityChangeResultEnum.NOSET
    };
    stateService.next(isClentsChangedEnter);
  }
  this.http.get<any>(`${this.apiUrl}/loans`, {
    headers: new HttpHeaders()
      // .set("Content-Type", "application/json")
      .set("Accept", "application/json")
  })
  .pipe(
    map((loans: any) => {
      // console.log('\tASYNC PIPE LoanListService.loadLoanList().map(ANY): %O', loans);
      if (this.isWrongLoanList(loans)) {
        return null;
      }
      if (!Array.isArray(loans) || loans.length === 0) {
        return null;
      }  
      let curLoanList = loans.map(item => {
        let loan: LoanInterface = {
          productName: item.productName,
          loanId: item.id.toString(),
          firstName: item.client.profile.firstName,
          lastName: item.client.profile.lastName,
          clientState: item.client.status,
          clientId: item.client.id,
          loanAmount: item.loanAmount,
          totalBalance: item.totalBalance,
          disbursementDate: item.disbursementDate,
          creditOfficer: item.creditOfficer,         // ??? makarov  
          lastModified: item.disbursementDate,

          loanStatus: item.status    // ??? makarov
        };
        return loan;
      })

      return {token: 'OK', loans: curLoanList};
    }),
    catchError((err: any): any =>{
      // console.log('\tASYNC PIPE LoanListService.loadLoanList().catchError() #2: %O', err);

      let msg: string;
      let isLoansChangedError: IsEntityChangedInterface = null;
      if (stateService) {
        isLoansChangedError = {
          op: EntityStateEnum.LOAD,
          isEnd: true,
          opResult: EntityChangeResultEnum.ERROR,
        };
      }
      if (err instanceof HttpErrorResponse) {
        // Server error 
        const status = err.status;
        if (status === 0) {
          msg = `SERVER ERROR: HTTP failure response for ${err.url}`;
        } else if (status > 0 && status < 600) {
            msg = `SERVER ERROR: ${status} - ${this.serverStatusSrv.getStatusText(status.toString())}`;
        }
        isLoansChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isLoansChangedError.message = msg;
      } else {
        // Loan error
        msg = err.message ? err.message : err.toString(); ;
        isLoansChangedError.messageType = EntityMessageTypeEnum.ERROR;
        isLoansChangedError.message = `CLIENT ERROR: ${msg}`;
      }
      if (stateService) {
        stateService.next(isLoansChangedError);
      }      
      this.clearLoanList();
      this.onDestroySub$.next(true);
      
      return of({token: 'ERROR', loans: null});
    }),
    takeUntil(this.onDestroySub$)
  ).subscribe(
    (rezObj: {token: string; loans: any[]} ) => {
      // console.log(`\tSUBSCRIBE: LoanListService.loadLoanList().subscribe(NEXT): rezObj: %O`, rezObj);

      // if (rezObj.token === 'ERROR') {
      //   if (stateService) {
      //     const isLoansChangedError: IsEntityChangedInterface = {
      //       op: EntityStateEnum.LOAD,
      //       isEnd: true,
      //       opResult: EntityChangeResultEnum.ERROR,
      //     };
      //     stateService.next(isLoansChangedError);
      //   }
      //   this.clearLoanList();
      // } else
       if (rezObj.token === 'OK') {
        this.curLoanListSub$.next(rezObj.loans); // BEFOR stateService.next()!
        if (stateService) {
          const isLoansChangedExit: IsEntityChangedInterface = {
            op: EntityStateEnum.LOAD,
            isEnd: true,
            opResult: EntityChangeResultEnum.SUCCESS
          };
          stateService.next(isLoansChangedExit);
        }
        // this.curLoanListSub$.next(rezObj.loans);
      }
    }
    // , err => console.log(`\tSUBSCRIBE: LoanListService.loadLoanList().subscribe(ERROR): %O`, err)
    // , () => console.log(`\tSUBSCRIBE: LoanListService.loadLoanList().subscribe(COMPLETE)`)
  );

}

}
