import { Component, OnInit, AfterViewInit, OnDestroy, ChangeDetectionStrategy, ViewChild, Input } from '@angular/core';
import { Router } from '@angular/router';

import { Observable, Subject } from 'rxjs';
import { tap, map, takeUntil } from 'rxjs/operators';

import { MatSnackBar, SimpleSnackBar, MatSnackBarRef } from '@angular/material';
import { MatTable, MatTableDataSource } from '@angular/material/table';

import { UserWithTokenInterface } from '../../auth/user.interface';
import { LoanInterface } from '../loan.interface';
import {
  IsEntityChangedInterface, EntityMessageTypeEnum, EntityStateEnum,
  EntityChangeResultEnum, OpStateInterface
}
  from '../../shared/model/entity-state.interface';

import { EntityStateService } from '../../shared/services/entity-state.service';
import { UserService } from '../../auth/user.service';
import { LoanListService } from '../loan-list.service';

import { isNgTemplate } from '@angular/compiler';

@Component({
  selector: 'app-loan-list',
  templateUrl: './loan-list.component.html',
  styleUrls: ['./loan-list.component.scss'],
  providers: [
    EntityStateService
  ],
  changeDetection: ChangeDetectionStrategy.OnPush,

})
export class LoanListComponent implements OnInit, OnDestroy {
  
  // @ViewChild(LoanTableComponent, {static: false}) loanTableCmp: LoanTableComponent;

  public currentUser$: Observable<UserWithTokenInterface> = null;
  public currentLoans$: Observable<any[] | LoanInterface[]> = null;
  
  // public curLoanList$: Observable<LoanInterface[]> = null;

  // --------------------------------------------------------------
  private simpleSnackBarRef: MatSnackBarRef<SimpleSnackBar> = null;
  public isLoansChanged$: Observable<OpStateInterface> = null;
  // --------------------------------------------------------------
  private onDestroySub$ = new Subject<boolean>();
  
  isFilter = false; // netesa
  
   private _loanView: string='';
   public get loanView(): string {
     return this._loanView;
   }
   public set loanView(val) {
      // console.log('LoanListComponent.loanView(val) val=%s', val);
      this._loanView = val;
   }
   private _renderLoanArr: LoanInterface[] = null;
   public get renderLoanArr() {
    return this._renderLoanArr;
   }
   public set renderLoanArr(val) {
     this._renderLoanArr = val;
   }


  constructor(
    private router: Router,
    protected userSrv: UserService,
    protected loansSrv: LoanListService,
    protected loansStateSrv: EntityStateService,
    private snackBarSrv: MatSnackBar
  ) {
    loansStateSrv.context = "LoanListComponent";
  }
    
  ngOnInit() {
    this.currentUser$ = this.userSrv.curUser$
      .pipe(
        tap((user: UserWithTokenInterface) => {
          // console.log(`\tPIPE: LoanListComponent.OnInit() tap currentUser$:%O`, user);
          if (this.userSrv.isAnonymUser(user)) {
            this.onDestroySub$.next(true);  // befor!
            this.loansSrv.clearLoanList();
            // this.onDestroySub$.next(true);
          }
        }),
        takeUntil(this.onDestroySub$)
      );
    this.currentLoans$ = this.loansSrv.curLoanList$
      .pipe(
        tap((loans: any[]) => {
          // console.log(`\tPIPE: LoanListComponent.OnInit() tap currentLoans$:%O`, loans,);

          if (this.loansSrv.isWrongLoanList(loans)) {
            if(this.userSrv.isCurUserAuth()){
              this.loansSrv.loadLoanList(this.loansStateSrv);
              // setTimeout(() => {
              //   this.loansSrv.loadLoanList(this.loansStateSrv);
              // }, 1200);
            }
          }
        }),
        map((loans: any[]) => {
          // console.log(`\tPIPE: LoanListComponent.OnInit() map currentLoans$:%O`, loans,);

          if (this.loansSrv.isWrongLoanList(loans)) {
            return null;
          }
          if (!Array.isArray(loans) || loans.length === 0) {
            return null;
          }          
          return loans;
        }),
        takeUntil(this.onDestroySub$)
      );
    this.isLoansChanged$ = this.loansStateSrv.isEntityChanged$
      .pipe(
          map((r: IsEntityChangedInterface): OpStateInterface   => {
          // console.log(`\tPIPE: LoanListComponent.OnInit() IsEntityChangedInterface:%O`, r,);
  
          if (r.op === EntityStateEnum.NOSET) {
            if (!r.isEnd && r.opResult === EntityChangeResultEnum.NOSET) {
              if (this.simpleSnackBarRef != null) {
                this.simpleSnackBarRef.dismiss();
                this.simpleSnackBarRef = null;
              }
              return {op: 'NOSET', isEnd: false, opResult: 'NOSET'};
            }
          } else if (r.op === EntityStateEnum.LOAD) {
            if (!r.isEnd && r.opResult === EntityChangeResultEnum.NOSET) {
              if (this.simpleSnackBarRef != null) {
                this.simpleSnackBarRef.dismiss();
                this.simpleSnackBarRef = null;
              }
              return {op: 'LOAD', isEnd: false, opResult: 'NOSET'};
            } else if (r.isEnd && r.opResult === EntityChangeResultEnum.ERROR) {
                this.simpleSnackBarRef = this.snackBarSrv.open(`${r.message}`,
                  'X', {
                  duration: 0,
                  panelClass: 'mat-snack-bar-container_err'
                });
                return {op: 'LOAD', isEnd: true, opResult: 'ERROR'};
           } else if (r.isEnd && r.opResult === EntityChangeResultEnum.SUCCESS) {
              // if (this.loanTableCmp) {
              //   this.loanTableCmp.dataSource.data = this.loansSrv.curLoanList; // OK
                
              //   // console.log(`\tPIPE: LoanListComponent.OnInit() LoanTableCmp:%O loansSrv.curLoanList:%O`,
              //   //               this.loanTableCmp, this.loansSrv.curLoanList);
              // }
              return {op: 'LOAD', isEnd: true, opResult: 'SUCCESS'};
            }
          } /* 
          else if (r.op === EntityStateEnum.DELETE) {
            if (!r.isEnd && r.opResult === EntityChangeResultEnum.NOSET) {
              if (this.simpleSnackBarRef != null) {
                this.simpleSnackBarRef.dismiss();
                this.simpleSnackBarRef = null;
              }
              return {op: 'DELETE', isEnd: false, opResult: 'NOSET'};
            } else if (!r.isEnd && r.opResult === EntityChangeResultEnum.ERROR) { // Error
              this.simpleSnackBarRef = this.snackBarSrv.open(`${r.message}`,
                'X', {
                duration: 0,
                panelClass: 'mat-snack-bar-container_err'
              });
              return {op: 'DELETE', isEnd: true, opResult: 'ERROR'};
            } else if (r.isEnd && r.opResult === EntityChangeResultEnum.SUCCESS) {
              this.simpleSnackBarRef =
                this.snackBarSrv.open(`Selected Loan has been deleted!`,
                'X', {
                duration: 0,
                panelClass: 'mat-snack-bar-container_info'
              });
              return {op: 'DELETE', isEnd: true, opResult: 'SUCCESS'};
            }
          } */
        })
      );    
  // ngOnInit       
  }
  

  buttonToggleChange(ev: any) {
    // console.log('buttonToggleChange ev -> %O', ev);

  }

  filter() {
    this.isFilter = !this.isFilter;
  }
  
  loanViewChanged(ev: string) {
    // console.log('LoanListComponent.loanViewChanged(ev) view=%s', ev);
    this.loanView = ev;
  }
  
  renderLoansChanged(ev: LoanInterface[]) {
    // console.log('LoanListComponent.renderLoansChanged(ev) loanArr=%O', ev);
    this.renderLoanArr = ev;
  }

  ngOnDestroy() {
    // this.loansSrv.clearLoanList(); // auto loaded

    if (this.simpleSnackBarRef != null) {
      this.simpleSnackBarRef.dismiss();
      this.simpleSnackBarRef = null;
    }
  }
} 