import { Component, OnInit, ViewChild, Input, 
  EventEmitter, ChangeDetectionStrategy, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { Observable } from 'rxjs';
import { tap, map } from 'rxjs/operators';

import { MatSort, Sort } from '@angular/material/sort';
import { MatTable, MatTableDataSource } from '@angular/material/table';

import { UserWithTokenInterface } from '../../../auth/user.interface';
import { LoanInterface } from '../../loan.interface';

import { IsEntityChangedInterface, EntityChangeResultEnum, 
        OpStateInterface } from '../../../shared/model/entity-state.interface';

// import { EntityStateService } from '../../../shared/services/entity-state.service';
// import { UserService } from '../../../auth/user.service';
// import { LoanListService } from '../../loan-list.service';

// import { LOAN_DATA } from './mock-data';

@Component({
  selector: 'app-loan-table',
  templateUrl: './loan-table.component.html',
  styleUrls: ['./loan-table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class LoanTableComponent implements OnInit {
  public currentUser$: Observable<UserWithTokenInterface> = null;
  public currentLoans$: Observable<LoanInterface[]> = null;
  public isLoansChanged$: Observable<OpStateInterface> = null;

  displayedColumns: string[] = [
    'productName',
    'loanId',
    'firstName',
    'lastName',
    'clientState',
    'loanAmount',
    'totalBalance',
    'disbursementDate',
    'creditOfficer',
    'lastModified',

    'loanStatus'        // // ??? makarov
  ];
  
  selectedLoan: LoanInterface | null;

  dataSource: MatTableDataSource<LoanInterface>;

  @Input('data-source') loanArr: LoanInterface[];
  
  private _loanView: string = '';
  @Input('loan-view')
  get loanView() {
    return this._loanView;
  }
  set loanView(val: string) {
    // console.log('LoanTableComponent.loanView(val) val=%s', val);
    this._loanView = val;
    this.dataSource.filter = val;
  }

  @ViewChild(MatTable, { static: true }) matTbl: MatTable<LoanInterface[]>;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  public renderLoanList$: Observable<LoanInterface[]> = null;
  
  @Output('render-loans') renderLoanListChange = new EventEmitter<LoanInterface[]>();

  constructor(
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.dataSource = new MatTableDataSource();
    // this.dataSource.sort = this.sort;
    this.dataSource.filterPredicate = (data, filter) => {
      // console.log('LoanTableComponent dataSource.filterPredicate() data=%O filter=%s',
      //               data, filter);

      const dataStr = data.loanStatus.toLowerCase().trim();
      return dataStr.indexOf(filter) != -1; 
    }
  }

  ngOnInit() {

    this.dataSource.data = this.loanArr;
    this.dataSource.sort = this.sort;
    
    const sortState: Sort = {active: 'loanId', direction: 'desc'};
    this.sort.active = sortState.active;
    this.sort.direction = sortState.direction;
    this.sort.sortChange.emit(sortState);


    this.renderLoanList$ = this.dataSource.connect()
      .asObservable()
      .pipe(
        tap((loans: LoanInterface[]) => {
          // console.log('\tPIPE: LoanTableComponent.OnInit() renderLoanList:%O', loans);
                    
          this.renderLoanListChange.emit(loans);
        })
      );    
  }

  onTblRowClick(loan: LoanInterface) {
    // console.log(`\tPIPE: LoanTableComponent.OnInit() clicked Loan:%O`, loan);
    
    // this.router.navigate([`/loans`, loan.loanId, 'overview']);
    // this.router.navigate([loan.loanId, 'loans'], {relativeTo: this.route});
    this.router.navigate([`/clients`, loan.clientId, 'loans']);
  }
}
